.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


html, body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}
h1, h2, h3, h4, h5, h6, ul, li, p, a, div {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  color: #000;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  font-weight: normal;
}

#main {
  max-width: 1444px;
  margin: 0 auto;
  overflow: hidden;
}


/* __________________________________________ [ MEDIA ] __________________________________ */

@media (max-width: 1460px) {
  #main {
    max-width: 1300px;
  }
}
@media (max-width: 1320px) {
  #main {
    max-width: 1100px;
  }
}
@media (max-width: 1200px) {
  #main {
    max-width: 900px;
  }
}
