
  /* _________________________________________ [ DESCTOP ] __________________________________ */

  .column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  .column:nth-child(2n) {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
  }
  .block {
    width: 50%;
  }

  .footer {
    border-style: solid;
    border-width: 2px;
    border-color: #e9e9e9;
    background-color: #efefef;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
  }
  .footer p,
  .footer a {
    font-size: 14px;
    color: #555559;
  }
  .footer-inner {
    padding: 28px 15px;
  }
  .contacts {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
  }
  .line {
    padding: 0 10px;
  }
