

  /* _________________________________________ [ DESCTOP ] __________________________________ */

  .mobile-menu-wrapper {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    user-select: none;
  }
  .mobile-menu-wrapper .mobile-menu {
    opacity: 0;
    visibility: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-align: center;
  }
  .mobile-menu-wrapper .mobile-menu > ul {
    width: 100%;
  }
  .mobile-menu-wrapper .mobile-menu > ul li {
    display: inline-block;
    width: 100%;
    -webkit-transition: transform 1s ease 0s;
    -o-transition: transform 1s ease 0s;
    transition: transform 1s ease 0s;
    opacity: 0;
    outline: none;
    font-size: 40px;
    display: block;
    color: #fff;
    padding: 15px;
    margin: 10px auto;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
  }
  .mobile-menu-wrapper .mobile-menu > ul :nth-child(odd) li {
    -webkit-transform: translate(-200vw, 0);
    -moz-transform: translate(-200vw, 0);
    -o-transform: translate(-200vw, 0);
    -ms-transform: translate(-200vw, 0);
    transform: translate(-200vw, 0);
  }
  .mobile-menu-wrapper .mobile-menu > ul :nth-child(even) li {
    -webkit-transform: translate(200vw, 0);
    -moz-transform: translate(200vw, 0);
    -o-transform: translate(200vw, 0);
    -ms-transform: translate(200vw, 0);
    transform: translate(200vw, 0);
  }

  .mobile-menu-wrapper .mobile-menu > ul li:focus {
    outline: none;
  }

  .mobile-menu-wrapper .mobile-menu > ul > li:focus {
    outline: none;
  }
  .mobile-menu-wrapper.active {
    opacity: 1;
    visibility: visible;
    height: 100vh;
    width: 100vw;
    background: #286782;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
  }
  .mobile-menu-wrapper.active .mobile-menu {
    opacity: 1;
    visibility: visible;
  }
  .mobile-menu-wrapper.active .mobile-menu > ul li {
    opacity: 1;
    transform: translateY(0px);
  }
  .mobile-menu-wrapper.active .mobile-menu > ul li:hover {
    background: #42a3cc;
  }
  .mobile-menu-wrapper.active .mobile-menu > ul li:hover {
    color: #000;
  }
  .mobile-controls {
    display: none;
    position: absolute;
    z-index: 11;
    right: 30px;
    top: 30px;
    cursor: pointer;
  }
  .mobile-controls img {
    width: 50px;
    height: 50px;
  }
  .mobile-controls .hotdog.active {
    opacity: 0;
    visibility: hidden;
  }
  .mobile-controls .close {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 0.2s ease-out;
    -o-transition: opacity 0.2s ease-out;
    transition: opacity 0.2s ease-out;
  }
  .mobile-controls .close.active {
    opacity: 1;
    visibility: visible;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    -webkit-transition: -webkit-transform 0.4s 0.3s ease-out;
    -o-transition: transform 0.4s 0.3s ease-out;
    transition: transform 0.4s 0.3s ease-out, -webkit-transform 0.4s 0.3s ease-out;
  }
  .header-inner {
    margin: 0 2px;
    box-shadow: 0px 2px 7.68px 0.32px rgba(0, 0, 0, 0.11);
  }
  .shadow {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    border-top: 2px solid #000000;
    -webkit-box-shadow: inset 0px 83px 27px -13px rgba(0, 0, 0, 0.49);
    -moz-box-shadow: inset 0px 83px 27px -13px rgba(0, 0, 0, 0.49);
    box-shadow: inset 0px 83px 27px -13px rgba(0, 0, 0, 0.49);
  }
  .hashtag {
    position: absolute;
    top: 15px;
    right: 30px;
  }
  .hashtag > a {
    margin-left: 10px;
    font-size: 15px;
    color: #ffffff;
    position: relative;
  }
  .hashtag > a::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    background-image: url('/icons/Instagram.png');
    background-size: cover;
    background-repeat: no-repeat;
    top: 0;
    left: -30px;
  }
  .photo-grid {
    width: 100%;
    position: relative;
  }
  .grid {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .photo {
    height: 250px;
    width: 17.2%;
  }
  .photo:last-child {
    width: 14%;
  }
  .desctop-menu {
    text-align: center;
  }
  .desctop-menu ul li {
    display: inline-block;
    padding: 30px 40px;
    color: #fff;
    font-size: 20px;
    color: #2e2e2e;
    text-transform: uppercase;
    font-weight: bold;
  }
 

  /* __________________________________________ [ MEDIA ] __________________________________ */
  @media (max-width: 1460px) {
    .photo {
      height: 235px;
    }
  }
  @media (max-width: 1320px) {
    .photo {
      height: 200px;
    }
  }
  @media (max-width: 1200px) {
    .photo {
      height: 160px;
    }
  }
  @media (max-width: 768px) {
    .desctop-menu ul li {
        padding: 30px 20px;
    }
  }
  @media (max-width: 600px) {
    .header-inner {
      display: none;
    }
    .mobile-controls {
      display: block;
    }
  }
