
  /* _________________________________________ [ DESCTOP ] __________________________________ */
    #contacts {
        width: 100%;
    }
  .button {
    margin-top: 30px;
  }
  .button button {
    border-radius: 6px;
    background-color: #286782;
    width: 200px;
    height: 50px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    border: none;
    outline: none;
  }
  .button button:focus {
    outline: none;
  }
  .button button:hover {
    background-color: #42a3cc;
    color: #000;
  }

  .form-wr {
    margin: 100px 0;
  }
  .form-inner {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0px 0px 20px 5px rgba(40, 43, 49, 0.16);
    width: 770px;
    height: 500px;
    margin: 0 auto;
  }
  .form-inner input::placeholder,
  .form-inner textarea::placeholder {
    font-size: 17px;
    color: #2e2e2e;
    text-align: left;
  }
  .form-inner .button.orange {
    width: 164px;
    height: 50px;
    margin-top: 30px;
  }
  .inputs-wr {
    margin-bottom: 20px;
    margin-top: 30px;
    text-align: center;
  }
  .inputs-wr input {
    font-size: 17px;
    text-align: left;
    color: #2e2e2e;
    border: 1px solid #e9e9e9;
    background-color: #efefef;
    border-radius: 6px;
    width: 280px;
    height: 50px;
    border-width: 1px;
    outline: none;
    padding-left: 20px;
    /* move placeholder right on focus*/
  }
  .inputs-wr input.focus {
    outline: none;
  }
  .inputs-wr input::-webkit-input-placeholder {
    text-indent: 0px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input::-moz-placeholder {
    text-indent: 0px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:-moz-placeholder {
    text-indent: 0px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:-ms-input-placeholder {
    text-indent: 0px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:focus::-webkit-input-placeholder {
    text-indent: 500px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:focus::-moz-placeholder {
    text-indent: 500px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:focus:-moz-placeholder {
    text-indent: 500px;
    transition: text-indent 0.5s ease;
  }
  .inputs-wr input:focus:-ms-input-placeholder {
    text-indent: 500px;
    transition: text-indent 0.5s ease;
  }
  #message {
    resize: none;
    border-style: solid;
    border-width: 1px;
    color: #2e2e2e;
    border: 1px solid #e9e9e9;
    background-color: #efefef;
    border-radius: 6px;
    width: 604px;
    height: 215px;
    outline: none;
    padding-left: 20px;
    padding-top: 30px;
    font-size: 17px;
    text-align: left;
  }
  #message.focus {
    outline: none;
  }
  #name {
    margin-right: 20px;
  }
  @media (max-width: 960px) {
    .form-wr {
      padding: 0 30px;
    }
    .form-inner .button.orange {
      margin-bottom: 50px;
    }
    .inputs-wr {
      width: 100%;
    }
    #message {
      width: 80%;
    }
    #name {
      width: 80%;
      margin: 50px 0 20px 0;
    }
    #e-mail {
      width: 80%;
    }
    .form-inner .button {
      margin-bottom: 50px;
    }
  }
  @media (max-width: 800px) {
    .form-inner {
      width: 590px;
    }
    .form-inner {
      width: 590px;
    }
  }
  @media (max-width: 600px) {
    .form-inner {
      width: 100%;
    }
  }
  