
  /* _________________________________________ [ DESCTOP ] __________________________________ */
 
  .intro {
    text-align: center;
    margin: 100px 0;
  }
  .intro > h2 {
    margin: 20px 100px;
    font-size: 56px;
    color: #555559;
  }
  .btn {
    margin-top: 100px;
  }
  .btn > a {
    border-radius: 6px;
    background-color: #286782;
    padding: 15px 30px;
    color: #fff;
  }
  .btn > a:hover {
    background-color: #42a3cc;
    color: #000;
  }
  .content-wr {
    margin-top: 180px;
  }
  .column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  .column:nth-child(2n) {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
  }
  .block {
    width: 50%;
  }
  .block-text {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .block-text-inner {
    padding: 50px 50px 50px 130px;
  }
  .block-title {
    margin-bottom: 40px;
  }
  .block-title > h2 {
    font-size: 24px;
    color: #555559;
    font-weight: bold;
    position: relative;
  }
  .block-title > h2::before {
    content: '';
    background-color: #286782;
    width: 30px;
    height: 3px;
    position: absolute;
    top: 16px;
    left: -55px;
  }
  .block-content > p {
    font-size: 18px;
    color: #555559;
  }
  .block-image {
    width: 100%;
    height: 100%;
  }
  .block-image img {
    width: 100%;
    height: 100%;
  }
  /* __________________________________________ [ MEDIA ] __________________________________ */
 
  @media (max-width: 1460px) {
    .photo {
      height: 235px;
    }
  }
  @media (max-width: 1320px) {
    .photo {
      height: 200px;
    }
  }
  @media (max-width: 1200px) {
    .photo {
      height: 160px;
    }
    .intro > h2 {
      margin: 20px 30px;
    }
    .block-text-inner {
      padding: 0px 40px 0px 100px;
    }
    .contacts {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }
  }
  @media (max-width: 800px) {
    .column {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }
    .column:nth-child(2n) {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column;
    }
    .block {
      width: 100%;
    }
    .block-text-inner {
      padding: 150px 50px 150px 150px;
    }
    .column:first-child .block-text-inner {
      padding-top: 50px;
    }
    .intro h2 {
      font-size: 48px;
    }
  }
  @media (max-width: 600px) {
    .intro h2 {
      font-size: 40px;
    }
    .block-text-inner {
      padding: 50px 50px 50px 100px;
    }
  }
  @media (max-width: 360px) {
    .block-title > h2::before {
      display: none;
    }
    .block-text-inner {
      padding: 30px 30px 30px 30px;
    }
    .intro {
      margin-top: 150px;
    }
    .intro h2 {
      font-size: 30px;
    }
    .intro-icon img {
      width: 70px;
      height: 70px;
    }
  }
  @media (max-width: 320px) {
    .intro > h2 {
      margin: 30px 10px;
    }
  }
  